.footer{
    background-color: $background-color;
    padding: 34px 0 30px;
    margin-top: 30px;
    color: $white;
    row-gap: 50px;

    @include on-tablet{
        padding: 50px 0 30px;
        margin-top: 60px;
    }

    @include on-desktop{
        margin-top: 90px;
    }

    &__contacts{
        display: flex;
        flex-direction: column;
        gap: 10px;
        grid-column: 1 / -1;

        @include on-tablet{
            gap: 30px;
            grid-column: 1 / 3;
        }

        @include on-desktop{
            grid-column: 1 / 5;
        }

        &__link{
            color: $white;
            text-decoration: none;
            font-family: Raleway, serif;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;

            @include hover(color, $accent-color);
        }

        &__socials{
            display: flex;
            gap: 20px;
            margin-top: 20px;
        }
    }

    &__timetable{
        display: flex;
        flex-direction: column;
        gap: 10px;
        grid-column: 1 / -1;

        @include on-tablet{
            gap: 30px;
            grid-column: 3 / 5;
        }

        @include on-desktop{
            grid-column: 5 / 9;
        }
    }

    &__links{
        display: flex;
        flex-direction: column;
        gap: 10px;
        grid-column: 1 / -1;

        @include on-tablet{
            gap: 30px;
            grid-column: 5 / -1;
        }

        @include on-desktop{
            grid-column: 9 / -1;
        }

        &__group{
            display: flex;
            flex-direction: column;
            gap: 5px;

            @include on-tablet{
                gap: 10px;
            }

            &__link{
                color: $white;
                text-decoration: none;
                font-family: Raleway, serif;
                font-size: 16px;
                font-weight: 400;
                line-height: 24px;
    
                @include hover(color, $accent-color);
            }
        }
    }


    &__bottom{
        grid-column: 1 / -1;
        display: flex;
        justify-content: space-between;
        padding-top: 10px;

        &__text{
            display: flex;
            align-items: center;
            gap: 22px;

            @include on-desktop{
                gap: 102px;
            }
        }
    }
}