.topbar{
    height: min-content;
    grid-column: 1 / -1;
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-content: center;

    @include on-desktop{
        padding: 32px 0;
    }
    
    &__icons{
        display: flex;
        gap: 30px;
        align-items: center;

        @include on-tablet{
            gap: 40px;
        }

        & > select{
            display: none;
            background-color: transparent;
            border: none;
            outline: none;
            color: $white;
            font-family: Raleway;
            font-size: 16px;
            font-weight: 500;
            line-height: 24px;
            border: none;

            @include on-tablet{
                display: flex;
            }

            @include hover(scale, (1.3));

            &:focus{
                scale: (1.3);
            }

            & > option{
                background-color: $background-color;
            }
        }
    }
}