.news{
    row-gap: 30px;
    margin-top: 30px;

    @include on-tablet{
        row-gap: 70px;
        margin-top: 60px;
    }

    @include on-desktop{
        margin-top: 90px;
    }

    &__title{
        text-align: center;
        grid-column: 1 / -1;

        @include on-tablet{
            text-align: left;
            grid-column: 1 / 5;
        }

        @include on-desktop{
            grid-column: 1 / 7;
            line-height: 100%;
        }
    }

    &__button{
        grid-column: 1 / -1;

        @include on-tablet{
            grid-column: 5 / -1;
        }

        @include on-desktop{
            grid-column: 10 / -1;
        }
    }

    &__card{
        grid-column: 1 / -1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 20px;
        grid-auto-rows: min-content;
        margin-top: 30px;

        @include on-tablet {
            margin-top: 0;
            grid-template-columns: repeat(3, 1fr);
            column-gap: 30px;
        }
      
        @include on-desktop {
            grid-template-columns: repeat(6, 1fr);
        }

        &--1{
            @include on-tablet{
                margin-top: 0;
                grid-column: 1 / 4;
            }

            @include on-desktop{
                grid-column: 1 / 7;
            }
        }

        &--2{
            @include on-tablet{
                grid-column: 4 / -1;
            }

            @include on-desktop{
                grid-column: 7 / -1;
            }
        }

        &__image{
            grid-column: 1 / -1;
            width: 100%;
            object-fit: cover;
            object-position: center;
            aspect-ratio: 1;

            @include on-desktop{
                aspect-ratio: 3 / 2;
            }

            @include hover(transform, scale(1.05));

            &--top{
                object-position: 50% 0;
            }
        }

        &__date{
            grid-column: 1 / -1;
            margin-top: 20px;

            @include on-tablet{
                margin-top: 30px;
            }

            @include on-desktop{
                margin-top: 20px;
            }
        }

        & > h4{
            display: inline-block;
            grid-column: 1 / -1;

            @media (min-width: 692px) {
                height: 60px;
            }

            @media (min-width: 697px) {
                height: 30px;
            }
        }

        &__description{
            grid-column: 1 / -1;
            margin-top: 20px;
        }
    }
}