.header{
    background-color: $background-color;
    grid-auto-rows: min-content;
    color: white;
    position: relative;

    @include on-desktop{
        height: 100vh;
        min-height: 800px;
    }

    &__date{
        grid-column: 1 / -1;

        @include on-tablet{
            margin-top: 100px;
        }

        @include on-desktop{
            margin-top: 80px;
        }
    }

    &__title{
        margin-top: 10px;
        grid-column: 1 / -1;

        @include on-tablet{
            grid-column: 1 / 6;
        }

        @include on-desktop{
            grid-column: 1 / 7;
        }
    }

    &__description{
        margin-top: 30px;
        grid-column: 1 / -1;

        @include on-tablet{
            grid-column: 1 / 4;
        }

        @include on-desktop{
            grid-column: 1 / 7;
        }
    }

    &__button{
        margin-top: 40px;
        grid-column: 1 / -1;

        @include on-tablet{
            margin-top: 50px;
            grid-column: 1 / 4;
        }

        @include on-desktop{
            margin-top: 80px;
            grid-column: 1 / 4;
        }
    }

    &__image{
        grid-column: 1 / -1;
        width: 100%;
        object-fit: cover;
        object-position: center;
        padding: 0 20px;
        box-sizing: border-box;
        pointer-events: none;
        max-height: 880px;

        @include on-tablet{
            grid-column: 2 / 6;
            margin-top: 50px;
            padding: 0;
        }

        @include on-desktop{
            grid-column: 7 / -1;
            width: 100%;
            margin-top: 0;
            position: absolute;
            right: -55;
            bottom: 0;
        }
    }

    &__bottom{
        display: none;
        grid-column: 1 / 7;
        width: 100%;
        gap: 20px;
        align-items: center;
        position: absolute;
        bottom: 50px;

        @include on-desktop{
            display: flex;
        }

        & > p{
            width: 115px;
        }

        &__devider{
            height: 1px;
            width: 100%;
            background-color: $white;
        }
    }
    
}