.menu{
    background-color: $background-color;
    grid-auto-rows: min-content;
    color: white;
    position: relative;
    width: 100vw;
    position: fixed;
    top: 0;
    bottom: 0;
    pointer-events: none;
    opacity: 0;
    transform: translateX(100%);
    transition: all 1s;
    z-index: 3;
    overflow: auto;

    @include on-desktop{
        height: 100vh;
        min-height: 800px;
    }

    &:target {
        opacity: 1;
        transform: translateX(0);
        pointer-events: all;
        overflow: auto;
    }

    &__timetable{
        grid-column: 1 / -1;
        margin-top: 10px;

        @include on-tablet{
            grid-column: 1 / 3;
            margin-top: 30px;
        }

        @include on-desktop{
            margin-top: 40px;
        }
    }

    &__address{
        grid-column: 1 / -1;
        margin-top: 20px;

        @include on-tablet{
            grid-column: 3 / -1;
            margin-top: 30px;
        }

        @include on-desktop{
            margin-top: 40px;
        }

        & > a{
            font-family: Raleway, serif;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            text-align: left;
            color: $white;
            text-decoration: none;

            @include hover(color, $accent-color);
        }
    }

    &__nav{
        grid-column: 1 / -1;
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        gap: 20px;

        @include on-tablet{
            margin-top: 50px;
            gap: 30px;
        }

        @include on-desktop{
            margin-top: 70px;
            gap: 60px;
        }

        & > a{
            color: $white;
            text-decoration: none;
            font-family: "Playfair Display", serif;
            font-weight: 700;
            font-size: 20px;
            line-height: 150%;
            height: 61px;
            width: fit-content;
            text-align: left;
            box-sizing: border-box;

            @include on-tablet{
                font-size: 48px;
                line-height: 125%;
            }

            @include on-desktop{
                font-size: 48px;
                line-height: 125%;
            }

            @include hover(border-bottom, 1px solid $white);
        }
    }

    &__devider{
        grid-column: 1 / -1;
        width: 100%;
        height: 1px;
        background-color: $white;
        margin: 40px 0;

        @include on-tablet{
            margin: 50px 0;
        }

        @include on-desktop{
            grid-column: 1 / 7;
            margin: 60px 0;
        }
    }

    &__button{
        grid-column: 1 / -1;
        margin-bottom: 50px;

        @include on-tablet{
            grid-column: 1 / 4;
        }
    }

    &__image{
        display: none;
        grid-column: 1 / -1;
        width: 100%;
        object-fit: cover;
        object-position: center;
        padding: 0 20px;
        box-sizing: border-box;
        pointer-events: none;
        max-height: 800px;

        @include on-tablet{
            display: flex;
            grid-column: 2 / 6;
            margin-top: 50px;
            padding: 0;
        }

        @include on-desktop{
            grid-column: 7 / -1;
            width: 100%;
            margin-top: 0;
            position: absolute;
            right: -55;
            bottom: 0;
        }
    }
}