.icon{
    display: flex;
    height: 20px;
    width: 20px;
    box-sizing: border-box;
    background-size: cover;
    background-position: center;
    background-origin: content-box;
    background-repeat: no-repeat;

    @include hover(transform, scale(1.3));

    &--no-hover {
        transition: none;
        &:hover {
          transform: none;
        }
    }

    &--logo{
        background-image: url('../images/logo_icon.png');
        height: 37px;
        width: 124px;
        padding: 6px 4px;
    }

    &--menu {
        background-image: url('../images/menu_icon.png');
        height: 30px;
        width: 30px;
        padding: 5px 3px;
    }
    
    &--close {
        background-image: url('../images/cross_icon.png');
        height: 30px;
        width: 30px;
        padding: 4px;
    }

    &--arrow-up {
        background-image: url('../images/arrow_up_icon.png');
        height: 30px;
        width: 30px;
        padding: 5px 6px;
    }
    
    &--fb {
        background-image: url('../images/fb_icon.png');
        padding: 0 5px;
    }
    
    &--tw {
        background-image: url('../images/tw_icon.png');
        padding: 2px 0;
    }

    &--tg {
        background-image: url('../images/tg_icon.png');
        padding: 0;
    }

    &--ig {
        background-image: url('../images/ig_icon.png');
        padding: 1px;
    }
}