.subscription{
    margin-top: 30px;

    @include on-tablet{
        margin-top: 60px;
    }

    @include on-desktop{
        margin-top: 90px;
    }

    &__title{
        grid-column: 1 / -1;

        @include on-tablet{
            grid-column: 1 / 6;
        }
    }

    &__description{
        grid-column: 1 / -1;
        margin-top: 20px;

        @include on-tablet{
            grid-column: 1 / 6;
        }

        @include on-desktop{
            grid-column: 7 / -1;
            margin-top: 0;
        }
    }

    &__form{
        display: grid;
        grid-template-columns: 1fr;
        grid-column: 1 / -1;
        margin-top: 50px;

        @include on-tablet{
            grid-column: 1 / 5;
            grid-template-columns: repeat(4, 1fr);
            column-gap: 30px;
        }

        @include on-desktop{
            grid-column: 1 / 6;
            grid-template-columns: repeat(5, 1fr);
        }

        &__input{
            grid-column: 1 / -1;
            background-color: transparent;
            padding-bottom: 3px;
            border: none;
            border-bottom: 1px solid $black;
            font-family: Raleway;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            transition: all 0.3s;

            &::placeholder{
                color: $black;
                font-weight: 400;
                transition: all 0.3s;
            }

            &:hover{
                border-bottom: 1px solid $accent-color;

                &::placeholder{
                    color: $accent-color;
                }
            }

            &:focus{
                border-bottom: 1px solid $accent-color;
                color: $accent-color;
                outline: none;

                &::placeholder{
                    color: $accent-color;
                }
            }
        }

        &__button{
            grid-column: 1 / 4;
            margin-top: 30px;

            @include on-desktop{
                margin-top: 40px;
            }
        }
    }

    
}