.events{
    row-gap: 30px;
    margin-top: 30px;

    @include on-tablet{
        row-gap: 70px;
        margin-top: 60px;
    }

    @include on-desktop{
        margin-top: 90px;
    }

    &__title{
        text-align: center;
        grid-column: 1 / -1;

        @include on-tablet{
            text-align: left;
            grid-column: 1 / 5;
        }

        @include on-desktop{
            grid-column: 1 / 7;
            line-height: 100%;
        }
    }

    &__button{
        grid-column: 1 / -1;

        @include on-tablet{
            grid-column: 5 / -1;
        }

        @include on-desktop{
            grid-column: 10 / -1;
        }
    }

    &__card{
        grid-column: 1 / -1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 20px;
        grid-auto-rows: min-content;
        margin-top: 30px;

        @include on-tablet {
            margin-top: 0;
            column-gap: 30px;
            grid-template-columns: repeat(6, 1fr);
        }
      
        @include on-desktop {
            grid-template-columns: repeat(12, 1fr);
        }

        &__image{
            grid-column: 1 / -1;
            width: 100%;
            object-fit: cover;
            object-position: center;
            aspect-ratio: 3.3 / 4;

            @include on-tablet{
                grid-column: 1 / 4;
            }

            @include on-desktop{
                grid-column: 1 / 5;
            }

            @include hover(transform, scale(1.05));
        }

        &__date{
            color: $accent-color;
            grid-column: 1 / -1;
            margin-top: 20px;

            @include on-tablet{
                margin-top: 0;
            }
        }

        &__title{
            grid-column: 1 / -1;
            margin-top: 10px;
        }

        &__description{
            grid-column: 1 / -1;
            margin-top: 20px;
        }

        &__button{
            grid-column: 1 / -1;
            margin-top: 30px;

            @include on-tablet{
                margin-top: 40px;
            }

            @include on-desktop{
                margin-top: 50px;
                width: 37.5%;
            }
        }

        &__text{
            display: flex;
            flex-direction: column;
            grid-column: 4 / -1;
            justify-content: flex-start;

            @include on-desktop{
                grid-column: 5 / -1;
            }
        }
    }
    
}